import {Component, Input, OnInit} from '@angular/core';
import {PolicyGroupService} from "app/org-management/services/policy-group.service";
import {IResponse} from "app/shared/interfaces/responces";
import {IOrgLocationModel} from "app/org-management/model/location";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {AbstractControl, FormControl} from "@angular/forms";
import {IGroupPolicies, LoadPoliciesParams} from "app/org-management/model/policy-group";

@Component({
  selector: 'load-policies-group',
  templateUrl: './load-policies-group.component.html',
  styleUrls: ['./load-policies-group.component.scss']
})
export class LoadPoliciesGroupComponent extends AppHelpers implements OnInit {
  @Input() control = new FormControl
  @Input() filterControl = AbstractControl as unknown as FormControl
  @Input() filterByLocation: boolean = false;
  @Input() filterByOrg: boolean = false;
  @Input() filterByJobTitle: boolean = false;
  @Input() submitted: boolean = false;
  policyGroup: IGroupPolicies[];
  selectedPolicyGroup: IGroupPolicies[] = [];

  constructor(private policyGroupService: PolicyGroupService) {
    super();
    // this.policyGroup = [
    //   {
    //     id: 1,
    //     ar_Name: "policy 1",
    //     en_Name: "type 1",
    //   },
    //
    //   {
    //     id: 2,
    //     ar_Name: "policy 2",
    //     en_Name: "type 2",
    //   },
    //
    //   {
    //     id: 3,
    //     ar_Name: "policy 3",
    //     en_Name: "type 3",
    //   },
    //
    //   {
    //     id: 4,
    //     ar_Name: "policy 4",
    //     en_Name: "type 4",
    //   },
    //
    //   {
    //     id: 5,
    //     ar_Name: "policy 5",
    //     en_Name: "type 5",
    //   },
    //
    //   {
    //     id: 6,
    //     ar_Name: "policy 6",
    //     en_Name: "type 6",
    //   },
    //
    // ];

  }


  ngOnInit(): void {
    this.filterControl?.valueChanges.subscribe((id) => {
      this.control.setValue(null);
      this.policyGroup = [];
      this.selectedPolicyGroup = [];

      if (id > 0) {
        this.loadPolicies();
      }
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.filterControl?.value > 0) this.loadPolicies();
  }

  loadPolicies() {
    let params: LoadPoliciesParams = {
      orgId: this.filterByOrg ? this.filterControl.value : null,
      jobTitleId: this.filterByJobTitle ? this.filterControl.value : null,
      locatonId: this.filterByLocation ? this.filterControl.value : null
    }

    this.policyGroupService
      .loadPolicies(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IGroupPolicies[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          console.log(res)
          this.policyGroup = res.data;
        } else this.errorList(res.message.messages);
      });
  }

  onMove($event) {
    let selected: number[] = []
    this.selectedPolicyGroup.forEach(e => {
      selected.push(e.id)
    })
    this.control.setValue(selected)
    console.log(this.control.value)

  }

}
