import {msgTranslate} from "app/shared/resources/helpers-functions";
import {IContactCategoryDisplay, IContactCategoryResponse} from "../models/contact-category";

export function contactCategoryToMap(contactCategories: IContactCategoryResponse[]): IContactCategoryDisplay[] {

  return contactCategories.map((category) => {
    return <IContactCategoryDisplay>{
      id: category.contactCategory_ID,
      ar_Name: category.comm_Contact_Category.ar_Name,
      en_Name: category.comm_Contact_Category.en_Name,
      icon: category.comm_Contact_Category.icon,
      contactType: msgTranslate(category.comm_Contact_Type.ar_Name, category.comm_Contact_Type.en_Name),
      Comm_Contact_Type_ID: category.contactType_ID,
    }
  });

}
