import { Component, OnInit, Inject } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import {
  IIdentityTypeModel,
  IIdentityTypeRequest,
} from "app/hr-setup/models/identity-type";
import { IdentityTypeService } from "app/hr-setup/services/identity-type.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-identity-type",
  templateUrl: "./identity-type.component.html",
  styleUrls: ["./identity-type.component.scss"],
})
export class IdentityTypeComponent extends AppHelpers implements OnInit {
  dataSource: MatTableDataSource<IIdentityTypeModel>;
  form: FormGroup;
  mandatoryColomns: string[] = [
    "Ser",
    "identity_TypeID",
    "identity_TypeTital",
    "isNationID",
    "actions",
  ];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];
  submitted: boolean = false;
  constructor(
    private identityTypeService: IdentityTypeService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<IdentityTypeComponent>
  ) {
    super();
  }

  ngOnInit(): void {
    // if (this.CheckNoBusinessOrCompany() ){
    // this.getList(this.currentPage);
    // }
    this.form = this.fb.group({
      identity_TypeID: [0, { nonNullable: true }],
      identity_TypeTital: [null, CustomValidator.whiteSpace],
      isNationID: [false],
    });
		if (this.data.value) {
			this.formCtrls.identity_TypeTital.setValue(this.data.value);
		}
  }
  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.identityTypeService
      .getList(params)
      .subscribe((res: IResponseList<IIdentityTypeModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: IIdentityTypeRequest = {
        identity_TypeID: this.form.value.identity_TypeID,
        identity_typetital: this.form.value.identity_TypeTital,
        isNationID: this.form.value.isNationID ?? false,
      };

      model.identity_TypeID == 0 ? this.create(model) : this.update(model);
    }
  }

  create(model: IIdentityTypeRequest) {
    this.showLoading();
    this.identityTypeService
      .create(model)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IIdentityTypeModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.getList(this.currentPage, this.page.pageSize);
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
        } else this.errorList(res.message.messages);
      });
  }

  update(model: IIdentityTypeRequest) {
    this.showLoading();
    this.identityTypeService
      .update(model)
      .subscribe((res: IResponse<IIdentityTypeModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.getList(this.currentPage, this.page.pageSize);
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        } else {
          this.errorList(res?.message?.messages);
        }
        this.hideLoading();
      });
  }

  identityToEdit(identity: IIdentityTypeModel) {
    this.form.patchValue(identity);
  }

  onDelete(identity: IIdentityTypeModel) {
    let msg = this.msgTranslate(identity.identity_TypeTital);
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.delete(identity);
      }
    });
  }

  delete(model: IIdentityTypeModel) {
    this.showLoading();
    this.identityTypeService
      .Delete(model.identity_TypeID)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);

          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            this.getList(this.prevPage, this.page.pageSize);
          } else {
            this.getList(this.currentPage, this.page.pageSize);
          }
          if (model.identity_TypeID == this.formCtrls.id.value)
            this.clearForm();
        } else {
          this.errorList(res?.message?.messages);
        }
      });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }

	onCloseDialog() {
		this.dialogRef.close();
	}
}
