import { msgTranslate } from "app/shared/resources/helpers-functions";
import { ILoanRuleModel } from "../models/loan-rules";
import { ILoanTypeModel } from "../models/loan-types";

export function loanTypeToMap(loanTypes: ILoanTypeModel[]): ILoanTypeModel[] {
  return loanTypes.map((loanType) => {
    return <ILoanTypeModel>{
      ...loanType,
      variableCode: loanType.paY_Loan_Type_Variable[0].pay_Variable.code,
      display_In_Result:
        loanType.paY_Loan_Type_Variable[0].pay_Variable.display_In_Result,
    };
  });
}

export function loanRuleToMap(loanRules: ILoanRuleModel[]): ILoanRuleModel[] {
  return loanRules.map((loanRule) => {
    return <ILoanRuleModel>{
      ...loanRule,
      jobTitle: msgTranslate(
        loanRule.core_Job_Title.ar_Name,
        loanRule.core_Job_Title.en_Name
      ),
      payment: msgTranslate(
        loanRule.pay_Payment.ar_Name,
        loanRule.pay_Payment.en_Name
      ),
      variable: msgTranslate(
        loanRule.pay_Variable.arName,
        loanRule.pay_Variable.name
      ),
    };
  });
}
