import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MessageTypes } from 'app/shared/enums/message-types.enum';
import { Idropdown } from 'app/shared/interfaces/dropdown.interface';
import { IResponse } from 'app/shared/interfaces/responces';
import { AppHelpers } from 'app/shared/resources/app-helpers';
import { ICourseCategoryModel } from 'app/training-setup/models/course-category';
import { CourseCategoryService } from 'app/training-setup/services/course-category.service';

@Component({
  selector: 'app-course-category-dropdown',
  templateUrl: './course-category-dropdown.component.html',
  styleUrls: ['./course-category-dropdown.component.scss']
})
export class CourseCategoryDropdownComponent extends AppHelpers implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "course-category";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<Idropdown[]>();
  courseCategoryList: ICourseCategoryModel[];

  constructor(private courseCategoryService:CourseCategoryService) {    super(); }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllCourseCategory();
  }

  ngOnInit(): void {
  }
  getAllCourseCategory() {
    this.courseCategoryService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res:IResponse<ICourseCategoryModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.courseCategoryList = res.data;
        } else this.errorList(res.message?.messages);
      });
  }
  onChange(values: Idropdown[]) {
    this.change.emit(values);
  }
}
