import {DatePipe, Location} from "@angular/common";
import {Component, inject} from "@angular/core";
import {FormBuilder} from "@angular/forms";
import {Title} from "@angular/platform-browser";
import {ActivatedRoute, Router} from "@angular/router";
import {AuthService} from "app/auth/services/auth.service";
import {UserViewsService} from "app/auth/services/user-views.service";
import {IGUIModel, IRowActionsModel} from "app/shared/interfaces/user-views";
import {Storage} from "app/shared/resources/local-storage";
import {IndividualConfig} from "ngx-toastr";
import {MessageService} from "primeng/api";
import {Subject, Subscription} from "rxjs";
import {IBusinessCompanyParams} from "../interfaces/params";
import {AssetsManager} from "./assets-manager";
import {DialogsService} from "../services/dialogs.service";
import {FileService} from "../services/file.service";
import {msgTranslate} from "./helpers-functions";
import {LanguageService} from "../services/language.service";
import {LoadingService} from "../services/loading.service";
import {MessagesService} from "../services/messages.service";
import {ParamsService} from "../services/params.service";
import {takeUntil} from "rxjs/operators";
import {Btn} from "app/shared/enums/btns.enum";

@Component({
    selector: "helpers-component",
    template: "",
})
export class AppHelpers {
    public dialogsService = inject(DialogsService);
    public messagesService = inject(MessagesService);
    public loadingService = inject(LoadingService);
    public messageService = inject(MessageService);
    public activatedRoute = inject(ActivatedRoute);
    public location = inject(Location);
    public router = inject(Router);
    public viewsService = inject(UserViewsService);
    public languageService = inject(LanguageService);
    public authService = inject(AuthService);
    public fb = inject(FormBuilder);
    public datePipe = inject(DatePipe);
    public title = inject(Title);
    public fileService = inject(FileService);
    public paramsService = inject(ParamsService);
    public assets: AssetsManager = new AssetsManager();
    public page: Page = new Page();

    personId?: number;
    moduleId?: number;
    parentModuleId?: number;
    rowFunctions: IRowActionsModel;
    globalFunctions: IGUIModel[];
    customViews: IGUIModel[];
    businessCompanyParams = {} as IBusinessCompanyParams;
    businessCompanyParamsSubscription: Subscription;
    pageFromUrl: number;
    btn = Btn;

    // using for cancel request on Distoray component
    private cancelRequest$ = new Subject<void>();

    constructor() {
        // start get params
        this.activatedRoute.params.subscribe((params) => {
            this.moduleId = params.moduleId;
        });

        // start get queryParamMap
        this.activatedRoute.queryParamMap.subscribe((params) => {
            this.parentModuleId = Number(params.get("parentModuleId"));
            this.pageFromUrl = Number(params.get("page"));
            // if (this.pageFromUrl > 0) this.page.pageIndex = this.pageFromUrl;

            // let size = Number(params.get("size"));
            // if (size > 0) this.page.pageSize = size;

            // if (this.getViewName?.includes("index")) this.removePageParams();
        });

        // Change Browser Page Title
        if (this.getViewName)
            this.title.setTitle("HRMS - " + (this.getViewName.toUpperCase() ?? ""));

        // this.userId = this.authService.getUserId;
        this.personId = Storage.getUserId;

        // businessCompanyParams subscribe
        this.businessCompanyParamsSubscription =
            this.paramsService.businessCompanyParams$.subscribe((result) => {
                this.businessCompanyParams = result;
                if (result.BusinessEntityID > 0 && result.CompanyID > 0) {
                    setTimeout(() => {
                        this.onChangeBusinessAndCompanyId();
                        // if (!this.getParentViewName) this.removePageParams();
                    }, -1);
                }
            });

        // get row & Global functions
        if (this.viewsService.UserModules?.length > 0) {
            this.onModulesChanged();
            this.globalFunctions = this.getGlobalFunctions(this.getViewName);
            this.rowFunctions = this.getRowFunctions(this.getViewName);
            this.customViews = this.getCustomViews(this.getParentViewName);
            if (this.getParentViewName)
                this.rowFunctions = this.getSubRowFunctions(
                    this.getParentViewName,
                    this.getViewName
                );
        }

        // subscribe  UserModules
        this.viewsService.UserModules$.subscribe((val) => {
            this.onModulesChanged();
            this.globalFunctions = this.getGlobalFunctions(this.getViewName);
            this.rowFunctions = this.getRowFunctions(this.getViewName);
            this.customViews = this.getCustomViews(this.getParentViewName);
            if (this.getParentViewName)
                this.rowFunctions = this.getSubRowFunctions(
                    this.getParentViewName,
                    this.getViewName
                );
        });
    } // end constractor

    onChangeBusinessAndCompanyId(): void {
        // to override in child components
    }

    onModulesChanged(): void {
        // to override in child components
    }

    checkNoBusinessOrCompany(): boolean {
        let objProp = (<any>Object)
            .values(this.businessCompanyParams)
            .every((value) => {
                if (value > 0) {
                    return true;
                }
            });
        return objProp;
    }

    ngOnInit(): void {
    }

    // get current route viewName
    get getViewName(): string {
        let view: string = null;
        this.activatedRoute.data.subscribe((data) => {
            view = data.view;
        });
        return view;
    }

    // get current route Parent viewName
    get getParentViewName(): string {
        let parentView: string = null;
        this.activatedRoute.queryParamMap.subscribe((params) => {
            parentView = params.get("parentView");
        });
        return parentView;
    }

    // get current route parentId
    get getParentId(): number {
        let parentId: number = null;
        this.activatedRoute.queryParamMap.subscribe((params) => {
            parentId = Number(params.get("parantId"));
        });
        return parentId;
    }

    // check is view is allowed for this role or no
    isViewAllowed(viewNamm: string): boolean {
        return this.viewsService.isViewAllowed(viewNamm);
    }

    getRowFunctions(viewName: string): IRowActionsModel {
        if (viewName) return this.viewsService.getRowFunctions(viewName);
    }

    getSubRowFunctions(parentView: string, viewName: string): IRowActionsModel {
        if (parentView && viewName)
            return this.viewsService.getSubRowFunctions(parentView, viewName);
    }

    getCustomViewChildren(parentView: string, viewName: string): IGUIModel[] {
        if (parentView && viewName)
            return this.viewsService.getCustomViewChildren(parentView, viewName);
    }

    getGlobalFunctions(viewName: string): IGUIModel[] {
        if (viewName) return this.viewsService.getGlobalFunctions(viewName);
    }

    getCustomViews(viewName: string): IGUIModel[] {
        if (viewName) return this.viewsService.getCustomViews(viewName);
    }

    removePageParams() {
        this.pageFromUrl = 0;
        const queryParams = {...this.activatedRoute.snapshot.queryParams};
        delete queryParams["page"];
        this.router.navigate([], {
            relativeTo: this.activatedRoute,
            queryParams: queryParams,
        });
    }

    // CompanyID & BusinessEntityID Params
    // get businessCompanyParams(): IBusinessCompanyParams {
    // 	return {
    // 		CompanyID: this.companyId,
    // 		BusinessEntityID: this.businessId,
    // 	};
    // }

    // Page Param
    get pageParams() {
        return {
            page: this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage,
            ...this.parantModuleIdParam,
            // size: this.page.pageSize,
        };
    }

    // Parent Module Id param
    get parantModuleIdParam() {
        return {
            parentModuleId: this.parentModuleId > 0 ? this.parentModuleId : null,
        };
    }

    // Parent Params
    get parantParam() {
        return {
            parentView: this.getParentViewName,
            ...this.parantModuleIdParam,
        };
    }

    // return all current page params
    get cuurentPageParams() {
        return {
            ...this.parantParam,
            ...this.pageParams,
        };
    }

    // navigate to url with params without reload [ parentModuleId, ParentView ]
    navigateTo(path: string) {
        const url = this.router
            .createUrlTree([path], {
                relativeTo: this.activatedRoute,
                queryParams: this.parantParam,
            })
            .toString();
        this.location.replaceState(url.toString());

        // this.router.navigate([url], { queryParams: this.parantParam });
    }

    get firstPage() {
        return 1;
    }

    get currentPage() {
        return this.page.pageIndex;
    }

    get prevPage() {
        return this.page.pageIndex - 1;
    }

    get nextPage() {
        return this.page.pageIndex + 1;
    }

    get lastPage() {
        return Math.floor(this.page.length / this.page.pageSize) + 1;
    }

    get lastPageDialog() {
        return Math.floor(this.page.length / this.page.dialogSize) + 1;
    }

    // translate message
    msgTranslate(arProp: string, enProp?: string): string {
        return msgTranslate(arProp, enProp);
    }

    // get localization by key
    translate(key: string): string {
        return this.languageService.translate(key);
    }

    deleateDialog(data: any) {
        return this.dialogsService.deleateDialog(data);
    }

    confirmDialog(data: any) {
        return this.dialogsService.confirmDialog(data);
    }

    //#region START: ngx Toaster
    success(
        message: string,
        title?: string,
        override?: Partial<IndividualConfig>
    ) {
        return this.messagesService.success(message, title, override);
    }

    warning(
        message: string,
        title?: string,
        override?: Partial<IndividualConfig>
    ) {
        return this.messagesService.warning(message, title, override);
    }

    error(message: string, title?: string, override?: Partial<IndividualConfig>) {
        return this.messagesService.error(message, title, override);
    }

    info(message: string, title?: string, override?: Partial<IndividualConfig>) {
        return this.messagesService.info(message, title, override);
    }

    errorList(errorsList: any[]) {
        return this.messagesService.errorList(errorsList);
    }

    infoList(errorsList: any[]) {
        return this.messagesService.infoList(errorsList);
    }

    // delete Successfully Toaster
    deleteSuccessfullyMsg(message) {
        return this.success("General.deleteSuccessfully", message);
    }

    // add Successfully Toaster
    addSuccessfullyMsg(message) {
        return this.success("General.addSuccessfully", message);
    }

    // update Successfully Toaster
    updateSuccessfullyMsg(message) {
        return this.success("General.updateSuccessfully", message);
    }

    //#endregion

    //#region START: Ngx Spiner
    showLoading() {
        this.loadingService.show();
    }

    hideLoading() {
        this.loadingService.hide();
    }

    //#endregion

    //#region START: Prime Toaster
    successPrime(message: string, title?: string) {
        this.messageService.add({
            severity: "success",
            summary: title,
            detail: message,
        });
    }

    infoPrime(message: string, title?: string) {
        this.messageService.add({
            severity: "info",
            summary: title,
            detail: message,
        });
    }

    warningPrime(message: string, title?: string) {
        this.messageService.add({
            severity: "warn",
            summary: title,
            detail: message,
        });
    }

    errorPrime(message: string, title?: string) {
        this.messageService.add({
            severity: "error",
            summary: title,
            detail: message,
        });
    }

    //#endregion

    // using for cancel request on Distoray component
    protected cancelRequest() {
        return takeUntil(this.cancelRequest$);
    }


    ngOnDestroy(): void {
        this.businessCompanyParamsSubscription.unsubscribe();
        // unsubscribe from all observables to prevent memory leaks
        this.cancelRequest$.next();
        this.cancelRequest$.complete();
    }
}

// START: Page Class
export class Page {
    pageSize: number;
    pageIndex: number;
    length: number;
    dialogSize: number;

    constructor(pageSize = 10, pageIndex = 1, length = 0, dialogSize = 5) {
        this.pageSize = pageSize;
        this.pageIndex = pageIndex;
        this.length = length;
        this.dialogSize = dialogSize;
    }
}
