import { Component, Input, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IAttachmenModel } from "app/general-setup/models/attachments";
import { HireDocumentsComponent } from "app/hr-setup/components/documents/hire-documents/hire-documents.component";
import { attachmentToMap } from "app/hr-setup/mapper/hire-document";
import { IHireDocumentsModel } from "app/hr-setup/models/hire-documents";
import { HireDocumentsService } from "app/hr-setup/services/hire-documents/hire-documents.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "app-hiring-document-by-service-dropdown",
  templateUrl: "./hiring-document-by-service-dropdown.component.html",
  styleUrls: ["./hiring-document-by-service-dropdown.component.scss"],
})
export class HiringDocumentByServiceDropdownComponent extends AppHelpers {
  @Input() control: FormControl;
  @Input() serviceControl?: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = "hiringDocument";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<IAttachmenModel[]>();
  viewName: string;

  serviceId: number;
  hiringDocumenttList: IAttachmenModel[] = [];

  constructor(private hireDocumentsService: HireDocumentsService) {
    super();
    this.viewName = ViewsManager.hire_documents;
  }

  ngOnInit(): void {
    // for employee
    if (!this.serviceControl) {
      this.serviceId = 8;
      this.hiringDocumenttList = null;
      this.getAllHiringDocument();
    } else {
      this.control.disable();

      this.serviceControl?.valueChanges.subscribe((serviceId) => {
        this.serviceId = serviceId;
        this.control.setValue(null);
        if (serviceId > 0) {
          this.getAllHiringDocument();
          if(!this.disable) this.control.enable();
        } else this.control.disable();
      });
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.serviceId > 0 && this.checkNoBusinessOrCompany())
      this.getAllHiringDocument();
  }

  getAllHiringDocument() {
    this.hireDocumentsService
      .GetAllByServiceId(this.serviceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IHireDocumentsModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.hiringDocumenttList = attachmentToMap(res.data);
        } else this.errorList(res.message?.messages);
      });
  }

  onChange(values: IAttachmenModel[]) {
    this.change.emit(values);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(HireDocumentsComponent, value, this.serviceId)
      .afterClosed()
      .subscribe((res) => {
        this.getAllHiringDocument();
      });
  }
}
