import {Component, Input, Output, EventEmitter} from "@angular/core";
import {AbstractControl, FormControl} from "@angular/forms";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {Idropdown} from "app/shared/interfaces/dropdown.interface";
import {IResponse} from "app/shared/interfaces/responces";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {EducationGradeService} from "app/hr-setup/services/education-grade.service";

@Component({
  selector: "app-education-grade-dropdown",
  templateUrl: "./education-grade-dropdown.component.html",
  styleUrls: ["./education-grade-dropdown.component.scss"],
})
export class EducationGradeDropdownComponent extends AppHelpers {
  @Input() control: FormControl | AbstractControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "grade";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<Idropdown[]>();

  gradeList: Idropdown[];

  constructor(private educationGradeService: EducationGradeService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllGrades();
  }

  getAllGrades() {
    this.educationGradeService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<Idropdown[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.gradeList = res.data;
        } else this.errorList(res.message.messages);
      });
  }

  onChange(values: Idropdown[]) {
    this.change.emit(values);
  }
}
