import {Injectable} from "@angular/core";
import {AppClientService} from "app/shared/services/app-client.service";
import {IResponse, IResponseList} from "app/shared/interfaces/responces";
import {IPageParams} from "app/shared/interfaces/params";
import {IOrgLocationModel, IOrgLocationRequest} from "app/org-management/model/location";
import {map} from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class OrgLocationService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IOrgLocationRequest) {
    return this.post<IResponse<IOrgLocationModel>>(
      "OrgManagement/Location/Create",
      data
    );
  }

  update(data: IOrgLocationRequest) {
    return this.post<IResponse<IOrgLocationModel>>(
      "OrgManagement/Location/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IOrgLocationModel[]>>(
      "OrgManagement/Location/GetList",
      {
        params: params,
      }
    );
  }

  getAll(govId: number) {
    return this.get<IResponse<IOrgLocationModel[]>>(
      "OrgManagement/Location/GetAll",
      {
        params: this.getGovIdFilter(govId)
      }
    ).pipe(
      map(res => {
        res.data.map(location => {
          location.ar_Name = location.location
          location.en_Name = location.location
        })
        return res;
      })
    )
      ;
  }

  Delete(id: number) {
    return this.delete<IResponse>("OrgManagement/Location/Delete", {
      params: {Id: id},
    });
  }


  getGovIdFilter(govId: number) {
    if (govId) return {$filter: `governament_ID eq ${govId}`}
    else return
  }
}
