import {Component, Input, OnInit} from "@angular/core";
import {FormControl} from "@angular/forms";
import {IOrganizationModel} from "app/org-management-setup/models/organizations";
import {OrgStructureService} from "app/org-management/services/org-structure.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {AppHelpers} from "app/shared/resources/app-helpers";

@Component({
	selector: "company-by-businessId-dropdown",
	templateUrl: "./company-dropdown.component.html",
	styleUrls: ["./company-dropdown.component.scss"],
})
export class CompanyDropdownComponent extends AppHelpers implements OnInit {
	@Input() control: FormControl;
	@Input() businessControl = new FormControl();
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() disable: boolean = false;
	@Input() disabledIds: number[];
	@Input() label: string = "Company";
	@Input() appendTo: string | HTMLElement;

	companyList: IOrganizationModel[];
	// private destroy$ = new Subject<void>();

	constructor(private orgStructureService: OrgStructureService) {
		super();
		if (this.businessControl.value) this.companyList = [];
	}

	ngOnInit(): void {
		this.control.disable();

		this.businessControl?.valueChanges.subscribe((businessId) => {
			//	this.control.setValue(null);
			if (businessId > 0) {
				this.getAllcompanysByBusiness();
				if (!this.disable) this.control.enable();
			} else this.control.disable();
		});

		if (!this.businessControl?.value) {
			this.businessControl?.setValue(this.businessCompanyParams.BusinessEntityID);
		}
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.businessControl?.value > 0 && this.checkNoBusinessOrCompany()) {
		}
		//	this.getAllcompanysByBusiness();
	}

	getAllcompanysByBusiness() {
		this.orgStructureService
			.getCurrentCompaniesByBusinessId(this.businessControl.value)
			.pipe(this.cancelRequest())
			.subscribe((res:any) => {
				if ((res.message.messageType = MessageTypes.Success)) {
					res.data.map((item) => {
						if (this.disabledIds?.includes(item.id)) item.isDisabled = true;
					});
					this.companyList = res.data;
					console.log("companyList", this.companyList);
				} else this.errorList(res.message.messages);
			});
	}

	// ngOnDestroy() {
	// 	// Unsubscribe from all observables to prevent memory leaks
	// 	this.destroy$.next();
	// 	this.destroy$.complete();
	// }
}
