import {Injectable} from "@angular/core";
import {BehaviorSubject, Subject} from "rxjs";
import {IBusinessCompanyParams} from "../interfaces/params";
import {Storage} from "../resources/local-storage";

@Injectable({
  providedIn: "root",
})
export class ParamsService {
  BusinessID$ = new Subject<number>();
  CompanyID$ = new Subject<number>();
  moduleId$ = new BehaviorSubject<number>(null);
  parentModuleId$ = new BehaviorSubject<number>(null);
  businessCompanyParams = {} as IBusinessCompanyParams;
  businessCompanyParams$ = new BehaviorSubject<IBusinessCompanyParams>(this.businessCompanyParams);

  constructor() {
    this.businessCompanyParams = {
      BusinessEntityID: Storage.getBusinessID,
      CompanyID: Storage.getCompanyID,
    };

    this.businessCompanyParams$.next(this.businessCompanyParams);

    // BusinessID subscribe
    this.BusinessID$.subscribe((val) => {
      if (this.businessCompanyParams.BusinessEntityID != val) {
        this.businessCompanyParams.BusinessEntityID = val;
        this.businessCompanyParams$.next(this.businessCompanyParams);
      }
    });

    // companyId subscribe
    this.CompanyID$.subscribe((val) => {
      if (this.businessCompanyParams.CompanyID != val) {
        this.businessCompanyParams.CompanyID = val;
        this.businessCompanyParams$.next(this.businessCompanyParams);
      }
    });
  }

  onSelectBusiness(value: number) {
    this.BusinessID$.next(value);
    Storage.setBusinessID(value);
  }

  onSelectCompany(value: number) {
    this.CompanyID$.next(value);
    Storage.setCompanyID(value);
  }
}
