import { Injectable } from "@angular/core";
import {
	IEducationDegreeModel,
	IEducationDegreeRequest,
} from "app/hr-setup/models/education-degree";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
	providedIn: "root",
})
export class EducationDegreeService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IEducationDegreeRequest) {
		return this.post<IResponse<IEducationDegreeModel>>("HRSetup/EducationDegree/Create", data);
	}

	update(data: IEducationDegreeRequest) {
		return this.post<IResponse<IEducationDegreeModel>>("HRSetup/EducationDegree/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IEducationDegreeModel[]>>("HRSetup/EducationDegree/GetList", {
			params: params,
		});
	}

	getAll() {
		return this.get<IResponse<IEducationDegreeModel[]>>("HRSetup/EducationDegree/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("HRSetup/EducationDegree/Delete", {
			params: { Id: id },
		});
	}
}
