import {
	HttpErrorResponse,
	HttpEvent,
	HttpHandler,
	HttpInterceptor,
	HttpRequest,
} from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Router } from "@angular/router";
import { LoadingService } from "app/shared/services/loading.service";
import { MessagesService } from "app/shared/services/messages.service";
import { EMPTY, Observable, throwError } from "rxjs";
import { catchError, finalize, retry } from "rxjs/operators";
import { AuthService } from "./auth.service";

@Injectable({
	providedIn: "root",
})
export class ErrorInterceptor implements HttpInterceptor {
	private totalRequests = 0;

	constructor(
		private authService: AuthService,
		private router: Router,
		private spinner: LoadingService,
		private toastr: MessagesService
	) {}

	intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
		this.totalRequests++;
		let stopRequests = false;
		if (stopRequests) {
			return EMPTY;
		}
		return next.handle(request).pipe(
			retry(1),
			catchError((err: HttpErrorResponse) => {
				let error;

				if ([401, 403].indexOf(err.status) !== -1) {
					// auto logout if 401 Unauthorized or 403 Forbidden
					this.toastr.error("Login Session has expired!");
					this.authService.logout();
					this.router.navigateByUrl("auth/login");
					return throwError("Session has expired!");
				} else if (err.status === 404) {
					// not Found
					// this.router.navigateByUrl("auth/not-found");
					error = err.message || err.statusText;
					this.toastr.error(error, "Error");
					this.spinner.hide();
					console.log("Error", error);
					return throwError(error);
				}

				if (err.status === 500 || err.status === 400) {
					// this.router.navigateByUrl("auth/error");
					error = err.message || err.statusText;
					this.toastr.error(error, "Error");
					this.spinner.hide();
					console.log("Error", error);

					return throwError(error);
				} else {
					error = err.message || err.statusText;
					this.toastr.error(error, "Error");
					this.spinner.hide();
					console.log("Error", error);

					// this.router.navigateByUrl("auth/error");

					return throwError(error);
				}
			}),
			finalize(() => {
				this.totalRequests--;
				if (this.totalRequests <= 0) {
					this.spinner.hide();
				}
			})
		);
	}
}
