import {Injectable} from '@angular/core';
import {IResponse} from "app/shared/interfaces/responces";
import {AppClientService} from "app/shared/services/app-client.service";
import {IGroupPolicies, LoadPoliciesParams} from "app/org-management/model/policy-group";

@Injectable({
  providedIn: 'root'
})
export class PolicyGroupService extends AppClientService {
  constructor() {
    super();
  }

  loadPolicies(params: LoadPoliciesParams) {
    return this.get<IResponse<IGroupPolicies[]>>("OrgManagement/PolicyGroup/loadPolicies", {
      params: this.loadPoliciesParams(params)
    });
  }

  getPolicesByCompany(params: { compId: number }) {
    return this.get<IResponse<IGroupPolicies[]>>("OrgManagement/PolicyGroup/GetPoliceiesByCompany", {
      params: params
    });
  }

  loadPoliciesParams(params: LoadPoliciesParams) {
    if (params.orgId) return {orgId: params.orgId}
    if (params.jobTitleId) return {jobTitleId: params.jobTitleId}
    if (params.locatonId) return {locatonId: params.locatonId}
  }
}



