import { IAttachmenModel } from "app/general-setup/models/attachments";
import {
  IDocJobTitleMap,
  IDocJobTitleModel,
  IHireDocMap,
  IHireDocumentsModel,
} from "../models/hire-documents";
import {
  HelpersFunctions,
  msgTranslate,
} from "app/shared/resources/helpers-functions";

export function hireDocumentToMap(
  documents: IHireDocumentsModel[]
): IHireDocMap[] {
  // get servicesIds list ex. [1,2,3]
  const servicesIds = HelpersFunctions.getMatched(documents, "serviceID");

  // Distinct comm_Attachment_Type for serviceId
  return servicesIds.map((serviceId) => {
    let obj = {} as IHireDocMap;
    obj.id = serviceId;
    obj.attachmentsList = [];
    obj.attachments = [];
    documents.map((e) => {
      if (e.serviceID == serviceId) {
        obj.ar_name = e.uM_Services.arbName;
        obj.en_name = e.uM_Services.engName;
        obj.attachmentsList.push(e.comm_Attachment_Type);
        obj.attachments.push(
          `  ${msgTranslate(
            e.comm_Attachment_Type.ar_Name,
            e.comm_Attachment_Type.en_Name
          )}  `
        );
      }
    });
    obj.attachments.join(" - ");
    return <IHireDocMap>{ ...obj };
  });
}

export function attachmentToMap(
  documents: IHireDocumentsModel[]
): IAttachmenModel[] {
  return documents.map((document) => {
    return <IAttachmenModel>{
      ...document.comm_Attachment_Type,
    };
  });
}

export function documentJobTitleToMap(
  documents: IDocJobTitleModel[]
): IDocJobTitleMap[] {
  return documents.map((document) => {
    return <IDocJobTitleMap>{
      jobTitleName: msgTranslate(
        document.core_Job_Title.ar_Name,
        document.core_Job_Title.en_Name
      ),
      attachment: msgTranslate(
        document.comm_Attachment_Type_Services.comm_Attachment_Type.ar_Name,
        document.comm_Attachment_Type_Services.comm_Attachment_Type.en_Name
      ),
    };
  });
}
