import {
  IUMUsers,
  IUserCompaniesModel,
  IUserCompanyToDisplay,
  IUserModel,
} from "../models/users";
import { msgTranslate } from "../../shared/resources/helpers-functions";

export function companyToMap(
  companies: IUserCompaniesModel[]
): IUserCompanyToDisplay[] {
  return companies.map((company) => {
    return <IUserCompanyToDisplay>{
      Business_Name: msgTranslate(
        company.core_Business_Entity.ar_Name,
        company.core_Business_Entity.en_Name
      ),
      Company_Name: msgTranslate(
        company.company.ar_Name,
        company.company.en_Name
      ),
      user_ID: company.user_ID,
      company_ID: company.company_ID,
      business_Entity_ID: company.business_Entity_ID,
      id: company.id,
    };
  });
}

export function userToMap(users: IUserModel[]): IUMUsers[] {
  return users.map((user) => {
    return <IUMUsers>{
      ...user.uM_Users,
    };
  });
}
