import { Component, Input, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IMaritalStatusModel } from "app/hr-setup/models/marital-status";
import { MaritalStatusService } from "app/hr-setup/services/marital-status.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-marital-status-dropdown",
  templateUrl: "./marital-status-dropdown.component.html",
  styleUrls: ["./marital-status-dropdown.component.scss"],
})
export class MaritalStatusDropdownComponent extends AppHelpers {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "maritalStatus";
  @Input() appendTo: string | HTMLElement;
	@Input() disable: boolean = false;
  @Output() change = new EventEmitter<IMaritalStatusModel[]>();

  maritalStatusList: IMaritalStatusModel[];

  constructor(private maritalStatusService: MaritalStatusService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllMaritalStatus();
  }

  getAllMaritalStatus() {
    this.maritalStatusService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IMaritalStatusModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.maritalStatusList = res.data;
        } else this.errorList(res.message.messages);
      });
  }

  onChange(values: IMaritalStatusModel[]) {
    this.change.emit(values);
  }
}
