import {Component, Input, OnInit} from "@angular/core";
import {AttLocationService} from "app/attendance-setup/services/att-location.service";
import {FormControl} from "@angular/forms";
import {INationalityModel} from "app/hr-setup/models/nationality";
import {ViewsManager} from "app/shared/resources/views-manager";
import {IResponse} from "app/shared/interfaces/responces";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {LocationComponent} from "app/attendance-setup/components/location/location.component";
import {IAttLocationModel} from "app/attendance-setup/models/att-location";
import {AppHelpers} from "app/shared/resources/app-helpers";

@Component({
  selector: "attendance-location-dropdown",
  templateUrl: "./att-location-dropdown.component.html",
  styleUrls: ["./att-location-dropdown.component.scss"],
})
export class AttLocationDropdownComponent extends AppHelpers implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "location";
  @Input() appendTo: string | HTMLElement;

  locationsList: IAttLocationModel[];
  viewName: string;

  constructor(private locationService: AttLocationService) {
    super();
    this.viewName = ViewsManager.location;
  }

  ngOnInit(): void {
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  getAll() {
    this.locationService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IAttLocationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.locationsList = res.data;
        } else this.errorList(res.message.messages);
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(LocationComponent, value)
      .afterClosed()
      .subscribe(() => {
        this.getAll();
      });
  }
}
